import React, { useEffect } from 'react';
import { connect } from 'react-redux';
import { withRouter, Redirect } from 'react-router';
import { compose } from 'redux';
import { setLists } from "../redux/lists-reducer";
import { originalImgPath, middleImgPath } from '../global/config';
import { Link } from 'react-router-dom';
const ListsPage = ({accessToken, accoundId, setLists, lists}) => {
    useEffect(() => {
        if (accessToken && accoundId) {
            setLists(accoundId)
            document.title = 'Мои списки'
        }
    }, [accessToken, accoundId])
    const textEnding = (num) => {
        // if(details.number_of_seasons%10===1){ }
        if (num % 10 >= 2 && num % 10 <= 4) {
            return 'а'
        } else if ((num % 10 >= 5 && num % 10 <= 9) || (num % 10 === 0 && num !== 1)) {
            return 'ов'
        } else {
            return ''
        }
    }
    if(!accessToken ){
        return  <Redirect  to='/'/>
    }
    return (
        <section>
            <div className="container-fluid">
                <h2 className="title">Мои списки:</h2>
                <div className="lists-grid">
                    {lists && lists.map(list=><Link to={`/lists/${list.id}`} className="lists-item" key={list.id} 
                    style={list.backdrop_path && { backgroundImage: `url(${middleImgPath}${list.backdrop_path})` }}
                    >
                        <div className="lists-item__name">{list.name}</div>
                        <div className="lists-item__items">{list.number_of_items}{ ` элемент${textEnding(list.number_of_items)} `}</div>
                    </Link>)}
                </div>
            </div>
        </section>
    );
};
const mapStateToProps = (state) => {
    return {
        token: state.auth.tokenV4,
        accoundId: state.auth.accountId,
        accessToken: state.auth.accessToken,
        lists: state.lists.lists
    }
}
export default compose(
    connect(mapStateToProps, { setLists }),
    withRouter,
)(ListsPage)